import cart from "../images/cart.svg"

// import { useState } from "react"
import React from "react"

import "./styles/sShopMedia.css"
import { ProductsDB, StoreDB } from "./Db"
import { Shopitem } from "./ShopItem"

const Shop = ({  shopcount, setShopcount }) => {


function plus (){
     setShopcount( shopcount + 1)
}


function add(data){
    const i = StoreDB.findIndex(e => e.id === data.id)
        if (i > -1){
            StoreDB[i].count = StoreDB[i].count + 1
        }else {
            StoreDB.push({...data, count: 1})
        }
    console.log(StoreDB)
}


    return ( <div className="container shops">
        <div className="top-w">
            <select name="" id="">
                <option value="Default">Default Sorting</option>
            </select>
            <span>Showing all 8 results</span>
        </div>
   
                <div className="extras">
        {
            ProductsDB.map(data => <Shopitem className="boxing" key={data.id}>
     
                <img  className='watches' src={data.image} alt="" />
                <h3>{data.name}</h3>
                <p>{data.category}</p>
                <div className="basket-item"><div className="line"></div>
                <div onClick={() => {add(data)}} className="ic"><img onClick={plus} className="bask" src={cart} alt="" />
                </div></div>
                <div className="price">{data.currency} {data.price}</div> 
         
            </Shopitem> )
        }
        </div>
     
    </div> );
}
 
export default Shop;