import logo from "../images/adeel-hassan-bird-logo-removebg-preview.png"
import basket from "../images/cart4.svg"
import sun from "../images/sun-svgrepo-com.svg"
import moon from "../images/moon-svgrepo-com.svg"
import can from "../images/x (2).svg"
import {NavLink} from "react-router-dom"
import { useState } from "react"
import "./styles/headerMedia.css"
function Header({theme , setTheme, shopcount, setShopcount}){

    
    function minus(){
        setShopcount(shopcount -1)
        if(shopcount === 0){
            setShopcount(shopcount -0)
        }
    }
    
    
    
    const  coun =  {
        width: '300px',
        height: '200px',
        display: 'flex',
        justifyContent: 'space-between',
        backgroundColor: '#34547A',
        position: 'absolute',
        top: '60px',
        left: '-100px',
    }
    
    const hides={
        display: 'none'
    }
    const navMenu ={
        width: "400px",
        height: '100vh',
        backgroundColor: 'gray',
        color: '#fff',
        right: '0',
        top: '0',
        transition: 'all .4s linear'
    }
    
    const [extra, setExtra] = useState('')
    const [shows, setShows] = useState(true)
    const [hide, setHide] = useState(true)
// console.log (shows)
    return(
        <header>
            <div className=" header-container">
            <div className="logo">
                <a><img src={logo}></img></a>
 
            </div>
           
        

            <nav className="navs-comp">
                <NavLink to="/" >Home</NavLink>
                <NavLink to="/gallery" >Gallery</NavLink>
                <NavLink to="/features" >Features</NavLink>
                <NavLink to="/reviews" >Reviews</NavLink>
                <NavLink to="/shop" >Shop</NavLink>
            </nav>
            <nav className="nav-link">

            <NavLink to="/shops" >
            <img src={basket} onClick={() => setShows(!shows)}  />

            </NavLink>
            <div className="btn-pur">{shopcount}</div>
            <div className="button">Buy Now</div>
           <div className="dl" onClick={() =>  setTheme(!theme)} >{theme ? <img src={moon}/> : <img src={sun} /> } </div>
           
           <div className="lines" onClick={() => setExtra(!extra)}  >
                <div className="line"></div>
                <div className="line"></div>
                <div className="line"></div>
            </div>

            <div className="nav-phone-extra" style={extra ? {...navMenu} : null} >
           <nav>
           <NavLink to="/" >Home</NavLink>
                <NavLink to="/gallery" >Gallery</NavLink>
                <NavLink to="/features" >Features</NavLink>
                <NavLink to="/reviews" >Reviews</NavLink>
                <NavLink to="/shop" >Shop</NavLink>
           </nav>
            </div>
           <div className="basket-item-source" style={shows ?  null : {...coun }  } >
                <p>hello</p>
                <p onClick={() => setHide(!hide)}>Close</p>
                <img onClick={minus} src={can} alt="cancel" />
           </div>
            </nav>


            </div>
            
        </header>
    )
}
export default Header