 import React, { Component } from "react"
 import $ from "jquery"


 export default class jQuery extends Component {

jQuerycode = () => {

$("button").click(function(){
if($('.ppp').css('color', 'red')){

    $('.ppp').css('color', 'yellow')
    $(this).css('background' , 'pink')
}
else {
    $('.ppp').css('color', 'black')
}
})
}

componentDidMount(){
    this.jQuerycode()
}
 render() {

return (

 <div>

 <p className="ppp">Lorem ipsum dolor sit amet consectetur, adipisicing elit. Adipisci vel voluptates sit voluptas illum
eveniet consectetur corrupti suscipit nesciunt et, inventore velit quaerat ex deleniti ut facere, voluptate
ab? Ducimus .</p>
 <button className="button" >Click here</button>

 </div>

)

 }
}