import { useEffect } from "react"
import ellips from "../images/Ellipse-974-copy-4.png"
import watches from "../images/Vector-Smart-Object-copy-3.png"
import AOS from "aos"
import "aos/dist/aos.css";
import "./styles/sHomeMedia.css"


function Main(){

useEffect(()=>{
    AOS.init();
    AOS.refresh();
}, [] )

    return (
            <div className="contain container">
                <div className="texts">
            <div data-aos="fade-right"
                 data-aos-offset="300"
                 data-aos-easing="ease-in-sine"
                 data-aos-duration="1000" className="leftside">
                 <h1>Lifestyle <br/> Smart Watch</h1>
            </div>

            <div   data-aos="fade-up"
                   data-aos-anchor-placement="bottom-bottom"  
                   data-aos-duration="1000"
             className="ddd">        
                    <p>TECHNOLOGY OF THE FUTURE</p>
                    <span>Technology is main part of this life. it makes easy life for us. <br/> Everyone cannot describe their life without gadgets </span>
             </div>
                </div>

            <div className="rightside"  data-aos="fade-left"
     data-aos-anchor="#example-anchor"
     data-aos-offset="500"
     data-aos-duration="1500" >
             <img src={watches} className="watching"/>
            </div>
            <div className="ellipss">

            <img src={ellips} className="ellips"/>
            </div>


        <div className="pannel">

        </div>

            </div>
    )
}
export default Main