import React from 'react'
import "./carousels.css"   //will be added later
import right from "../../images/chevron-right (1).svg"
import left from '../../images/chevron-left.svg'
import { useState, useEffect } from 'react'

const Carousels = (props) => {
    const {children} = props

 const [currentIndex, setCurrentIndex] = useState(0)
const [length, setLength] = useState(children.length)

useEffect(() => {
    setLength(children.length)
}, [children])

const next = () => {
    if (currentIndex < (length - 1)) {
    setCurrentIndex(prevState => prevState + 1)
}
}

const prev = () => {
if (currentIndex > 0) {
    setCurrentIndex(prevState => prevState - 1)
}
}


return (
    <div className="carousel-containers">
       
        <div className="carousel-wrappers">
    {/* You can alwas change the content of the button to other things */}
    {/* <button className="left-arrow">
        &lt;
    </button> */}
      <button onClick={prev} className="left-arrow">
    <img src={left} alt="" />
    </button>
    <div className="carousel-content-wrappers">
       
    <div
        className="carousel-contents"
        style={{ transform: `translateX(-${currentIndex * 100}%)` }}
    >
        {children}
    </div>
</div>
    {/* You can alwas change the content of the button to other things */}
  

<button onClick={next} className="right-arrow">
    <img src={right} alt="" />
</button>
</div>
        
    </div>
)
 
    
    
}
export default Carousels