import w1 from "../images/w1.PNG"
import w2 from "../images/w2.PNG"
import w3 from "../images/w3.PNG"
import AOS from "aos"
import "aos/dist/aos.css";
import { useEffect } from "react";



const Mainfive = () => {

useEffect(()=> {
    AOS.init()
    AOS.refresh()
}, [])


    return (
         <div className="container black-container">
            <h4>ADJUSTABLE STRAPS</h4>
            <p>Choose the best color for your activity</p>

            <div className="carusel-two">
                <div data-aos="flip-left" data-aos-duration="2000" className="item-c-one">
                    <img src={w1} alt="w1" />
                </div>
                <div data-aos="flip-left" data-aos-duration="2000" className="item-c-one">
                    <img src={w2} alt="w2" />
                </div>
                <div data-aos="flip-left" data-aos-duration="2000" className="item-c-one">
                    <img src={w3} alt="w3" />
                </div>
            </div>
                    <button className="buttons-btn"><a href="">See More</a></button>

        </div>
     );
}
 
export default Mainfive;