import { StoreDB } from "./Db"
import { BasketItem } from "./ShopItem"
import cancel from "../images/x (2).svg"
import { useState } from "react"
const Basket = ({setShopcount}) => {

    const [ del, setDelete] = useState(StoreDB)
    function cancelItem(){
        // del.filter(e => )
        for(let i = StoreDB.length; i >  0; i-- ){

            setDelete(del.filter(e => del.pop(e)))
            setShopcount(0)   
        }
        console.log(del)
    }

    return (
        <div>

            {
                StoreDB.map(data => <BasketItem key={data.id}>
                    <table className="table-item" >
                        <thead>

                            <tr>

                                <td className="td" ></td>
                                <td className="product-name">{data.count}</td>
                                <td className="product-name" >Product</td>
                                <td className="p-price" >Price</td>
                                <td className="p-quantity" >Quantity</td>
                                <td className="p-sub" >Subtotal</td>
                            </tr>
                        </thead>

                        <tr>

                            <td className="tds" ><img src={cancel} onClick={() => {cancelItem()}} alt="cancel" /></td>
                            <td className="p-img" ><img src={data.image} alt="watch" /></td>
                            <td className="p-name" >{data.name}</td>
                            <td className="p-price" >{data.currency} {data.price}</td>
                            <td className="p-pp" ><button>T</button> <button>P</button> </td>
                            <td className="p-price" >{data.currency} {data.price * data.count}</td>
                        </tr>
                        <tr>
                            <td colSpan={6} className="update">
                                <div className="app">
                                    <div className="extra">
                                        <input type="text" placeholder="Coupon code" />
                                        <button>APPLY COUPON</button>
                                    </div>
                                    <button>UPDATE CARD</button>
                                </div>

                            </td>
                        </tr>
                    </table>

                </BasketItem>)
            }
        </div>
    );
}

export default Basket;