import watch1 from "../../images/gallery6.jpg"
import watch2 from "../../images/gallery5.jpg"
import watch3 from "../../images/gallery7.jpg"
import watch4 from "../../images/Watchwhite.jpg"
import watch5 from "../../images/Watchwhite2.jpg"
import watch6 from "../../images/Watchwhite3.jpg"
import watch7 from '../../images/Watchwhite4.jpg'
import watch8 from '../../images/Watchwhite5.jpg'
import watch9 from '../../images/Watchwhite3 (1).jpg'
import Carousels from "./Carousels"
import "../styles/sGalleryMedia.css"


function Car() {



    return (



        <div className="immm" style={{ maxWidth: 1200, marginLeft: 'auto', marginRight: 'auto', }}>
            { }


            <Carousels>

                <div className="dls">
                    <div className="con">
                        <img src={watch1} alt="placeholder" />
                    </div>
                    <div className="con">
                        <img src={watch2} alt="placeholder" />
                    </div>
                    <div className="con">
                        <img src={watch3} alt="placeholder" />
                    </div>
                </div>
                <div className="dls">
                    <div className="con">
                    <img src={watch4} alt="placeholder" />
                    </div>
                    <div className="con">
                    <img src={watch5} alt="placeholder" />
                    </div>
                    <div className="con">
                    <img src={watch6} alt="placeholder" />
                    </div>
                </div>
                <div className="dls">
                    <div className="con">
                    <img src={watch7} alt="placeholder" />
                    </div>
                    <div className="con">
                    <img src={watch8} alt="placeholder" />
                    </div>
                  <div className="con">
                  <img src={watch9} alt="placeholder" />
                  </div>
                </div>



            </Carousels>
        </div>







    )
}
export default Car



