import Table from "./Table";
import Basket from "./Basket"
const Shopcount = ({setShopcount}) => {



    return ( <div className="container shop-count " >

            <h1>Shop</h1>    
            <div className="view-card">
                    <div className="view">View Cart</div>
                    <div className="view">Checkout</div>
                    <div className="view">Done</div>
            </div>
                <Basket setShopcount={setShopcount} />
          
    </div> );
}
 
export default Shopcount;