import React from 'react'
import './Carousel.css' //will be added later
import right from "../images/chevron-right (1).svg"
import left from '../images/chevron-left.svg'
import { useState, useEffect } from 'react'

const Carousel = (props) => {
    const {children} = props

 const [currentIndex, setCurrentIndex] = useState(0)
const [length, setLength] = useState(children.length)

useEffect(() => {
    setLength(children.length)
}, [children])

const next = () => {
    if (currentIndex < (length - 1)) {
    setCurrentIndex(prevState => prevState + 1)
}
}

const prev = () => {
if (currentIndex > 0) {
    setCurrentIndex(prevState => prevState - 1)
}
}
let names = [
    {name: 'Kamol'},
    {name: 'Viktoria Cyan'},
   
]


const [ themes, setThemes] = useState(names[0].name)
// const [themes2, setThemes2] = useState(names[1].name)
// console.log(names.filter(e => e.length.n))

const handleClick = () => {
    const wordSlider = names[0].name
    setThemes(wordSlider)
}

const handleClick2 = () => {
    const wordSlider = names[1].name
    setThemes(wordSlider)
}



    return (
        <div className='container cryt'>

        
     <div className="carousel-container">
      <div className="carousel-wrapper">
    {names.map((data, i) => 
    <span key={i} onClick={() => handleClick(i)}  >    <button onClick={prev} className="left-arrow">
    <img src={left} />
    </button></span>
  )}  
    <div className="carousel-content-wrapper">
    <div
        className="carousel-content "
        style={{ transform: `translateX(-${currentIndex * 100}%)` }} >
        {children}
    </div>
    </div>
    {/* You can alwas change the content of the button to other things */}





{names.map((data, i) => 
<span key={i} onClick={() => handleClick2(i)}  >    <button onClick={next} className="right-arrow">
    <img src={right} />
</button></span>
  )}  



</div>
        </div>

        <div className='et'>
        <p>Lorem ipsum dolor sit amet consectetur adipisicing elit. Vero obcaecati quod facere voluptatem rerum sunt! Qui in assumenda, maxime laboriosam eum provident sint obcaecati aut hic amet, quod voluptas architecto?</p>
        <span>{themes ? themes : themes}</span>
        <div>99</div>
        </div>
        </div>
    )
    
    
    
}
export default Carousel
